/* vdl-color using default palette 'vdl-palette-1'
  Examples of use:
  vdl-color(15);

  to specify a different color palette:
  vdl-color(4, $some-other-palette);
 */
.vdl-tab-header {
  overflow: hidden;
  position: relative;
  display: flex;
  flex-direction: row;
  flex-shrink: 0; }

.vdl-tab-label {
  line-height: 40px;
  height: 40px;
  margin: 0;
  padding: 0 15px;
  font-size: 16px;
  font-family: Roboto, sans-serif;
  font-weight: 500;
  cursor: pointer;
  box-sizing: border-box;
  color: #414142;
  min-width: 160px;
  text-align: center;
  border-radius: 3px;
  transition: border-bottom 4ms ease-in-out 3ms;
  position: relative; }
  .vdl-tab-label:focus, .vdl-tab-label.focus {
    outline: none; }
  .vdl-tab-label:hover, .vdl-tab-label.hover {
    background-color: #f3f3f3; }
  .vdl-tab-label.vdl-tab-link-active, .vdl-tab-label.vdl-tab-label-active {
    color: #005a99; }
  .vdl-tab-label.vdl-tab-disabled {
    cursor: default;
    pointer-events: none;
    color: #a5a5a5; }

@media (max-width: 600px) {
  .vdl-tab-label {
    min-width: 72px; } }

vdl-ink-bar {
  position: absolute;
  bottom: 0;
  height: 2px;
  background-color: #005a99;
  transition: 500ms cubic-bezier(0.35, 0, 0.25, 1); }
  .vdl-tab-group-inverted-header vdl-ink-bar {
    bottom: auto;
    top: 0; }

.vdl-tab-header-pagination {
  position: relative;
  display: none;
  justify-content: center;
  align-items: center;
  min-width: 32px;
  cursor: pointer;
  z-index: 2; }
  .vdl-tab-header-pagination-controls-enabled .vdl-tab-header-pagination {
    display: flex; }

.vdl-tab-header-pagination-before, .vdl-tab-header-rtl .vdl-tab-header-pagination-after {
  padding-left: 4px; }
  .vdl-tab-header-pagination-before .vdl-tab-header-pagination-chevron, .vdl-tab-header-rtl .vdl-tab-header-pagination-after .vdl-tab-header-pagination-chevron {
    transform: rotate(-135deg); }

.vdl-tab-header-rtl .vdl-tab-header-pagination-before, .vdl-tab-header-pagination-after {
  padding-right: 4px; }
  .vdl-tab-header-rtl .vdl-tab-header-pagination-before .vdl-tab-header-pagination-chevron, .vdl-tab-header-pagination-after .vdl-tab-header-pagination-chevron {
    transform: rotate(45deg); }

.vdl-tab-header-pagination-chevron {
  border-style: solid;
  border-width: 2px 2px 0 0;
  content: '';
  height: 8px;
  width: 8px; }

.vdl-tab-header-pagination-disabled {
  box-shadow: none;
  cursor: default; }
  .vdl-tab-header-pagination-disabled .vdl-tab-header-pagination-chevron {
    border-color: #ccc; }

.vdl-tab-label-container {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
  z-index: 1; }

.vdl-tab-list {
  display: flex;
  flex-grow: 1;
  position: relative;
  transition: transform 500ms cubic-bezier(0.35, 0, 0.25, 1); }
